import './App.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import Navbar from "./components/Navbar"
import Pokemons from "./components/Pokemons"
import MyPokemonList from "./components/MyPokemonList"
import PokemonDetail from "./components/PokemonDetail"
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'

function App() {
  return (
    <Router basename='/pokemon-list-react/'>
    <Navbar/>
    <Switch>
      <Route exact path={`${process.env.PUBLIC_URL}/`} component={Pokemons} />
      <Route exact path={`${process.env.PUBLIC_URL}/pokemonList`} component={Pokemons} />
      <Route exact path="/myPokemonList" component={Pokemons} />
      <Route exact path="/pokemon/:id" component={PokemonDetail} />
    </Switch>
    </Router>
  )
}

export default App;
